body {
  background-color: rgba(255, 253, 235, 1);
}

.container {
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    padding-right: 1rem;
    padding-left: 1rem
}
@media (min-width: 375px) {
    .container {
        max-width: 375px;
        padding-right: 1rem;
        padding-left: 1rem
    }
}
@media (min-width: 640px) {
    .container {
        max-width: 640px;
        padding-right: 2rem;
        padding-left: 2rem
    }
}
@media (min-width: 768px) {
    .container {
        max-width: 768px
    }
}
@media (min-width: 1024px) {
    .container {
        max-width: 1024px;
        padding-right: 4rem;
        padding-left: 4rem
    }
}
@media (min-width: 1280px) {
    .container {
        max-width: 1280px;
        padding-right: 5rem;
        padding-left: 5rem
    }
}
@media (min-width: 1536px) {
    .container {
        max-width: 1536px;
        padding-right: 6rem;
        padding-left: 6rem
    }
}
@media (min-width: 1800px) {
    .container {
        max-width: 1800px
    }
}
  .cards {
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    padding-right: 1rem;
    padding-left: 1rem
}
  @media (min-width: 375px) {
    .cards {
        max-width: 375px;
        padding-right: 1rem;
        padding-left: 1rem
    }
}
  @media (min-width: 640px) {
    .cards {
        max-width: 640px;
        padding-right: 2rem;
        padding-left: 2rem
    }
}
  @media (min-width: 768px) {
    .cards {
        max-width: 768px
    }
}
  @media (min-width: 1024px) {
    .cards {
        max-width: 1024px;
        padding-right: 4rem;
        padding-left: 4rem
    }
}
  @media (min-width: 1280px) {
    .cards {
        max-width: 1280px;
        padding-right: 5rem;
        padding-left: 5rem
    }
}
  @media (min-width: 1536px) {
    .cards {
        max-width: 1536px;
        padding-right: 6rem;
        padding-left: 6rem
    }
}
  @media (min-width: 1800px) {
    .cards {
        max-width: 1800px
    }
}
  .cards {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem
}
  @media (min-width: 768px) {
    .cards {
        flex-direction: row
    }
    .cards {
        flex-wrap: wrap
    }
    .cards {
        align-items: stretch
    }
    .cards {
        justify-content: center
    }
}

  .card {
    margin-bottom: 3rem;
    display: flex;
    max-width: 20rem;
    flex-direction: column;
    align-items: center;
    justify-content: center
}

  @media (min-width: 768px) {
    .card {
        justify-content: flex-start
    }
}

  .card__img {
    margin-bottom: 1.5rem;
    height: 24rem;
    width: 100%;
    border-radius: 0.125rem;
    --tw-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
    box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow)
}

  @media (min-width: 640px) {
    .card__img {
        height: 460px
    }
}

  .card__title {
    margin-bottom: 0.5rem;
    font-size: 1.5rem;
    line-height: 2rem
}

  .card__subtitle {
    margin-bottom: 0.5rem;
    text-align: center
}

  .hover\:bg-yellow-500:hover {
    --tw-bg-opacity: 1;
    background-color: rgba(245, 158, 11, var(--tw-bg-opacity))
}

  @media (min-width: 768px) {
    .md\:block {
        display: block
    }
    .md\:hidden {
        display: none
    }
    .md\:text-7xl {
        font-size: 4.5rem;
        line-height: 1
    }
}

  @media (min-width: 1024px) {
    .lg\:text-9xl {
        font-size: 8rem;
        line-height: 1
    }
}


