  .hideNavDeropDown {
    position: fixed;
    top: 0px;
    left: -100%;
    z-index: 10;
    display: flex;
    height: 100vh;
    width: 50%;
    cursor: pointer;
    flex-direction: column;
    align-items: center
}
  .hideNavDeropDown > :not([hidden]) ~ :not([hidden]) {
    --tw-space-y-reverse: 0;
    margin-top: calc(0.5rem * calc(1 - var(--tw-space-y-reverse)));
    margin-bottom: calc(0.5rem * var(--tw-space-y-reverse))
}
